import React, { useState, useEffect } from "react";
import { Download, Upload, FileText, Eye, Trash2, Plus } from "lucide-react";
import { Document, Page, pdfjs } from 'react-pdf';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import 'react-pdf/dist/esm/Page/TextLayer.css';

// Configure PDF.js worker for react-pdf v6
pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;

const PDFViewer = () => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [scale, setScale] = useState(1.0);
  const [uploadedFiles, setUploadedFiles] = useState([
    {
      id: 1,
      name: "Mock_New_Jersey_Unemployment_Insurance.pdf",
      version: "v3",
      size: "2.7 KB",
      uploadDate: "2024-01-15",
      description: "New Jersey Unemployment Insurance Documentation",
      url: "/Mock_New_Jersey_Unemployment_Insurance.pdf",
      isRealPDF: true
    },
    {
      id: 2,
      name: "Mock_New_Jersey_Unemployment_Insurance.pdf",
      version: "v2",
      size: "2.6 KB",
      uploadDate: "2024-01-10",
      description: "Updated formatting and content",
      url: "#",
      isRealPDF: false
    },
    {
      id: 3,
      name: "Mock_New_Jersey_Unemployment_Insurance.pdf",
      version: "v1",
      size: "2.5 KB",
      uploadDate: "2024-01-08",
      description: "Basic structure and content",
      url: "#",
      isRealPDF: false
    }
  ]);

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file && file.type === "application/pdf") {
      // Calculate new version number
      const currentVersions = uploadedFiles.filter(f => f.name === file.name);
      const nextVersion = currentVersions.length + 1;
      const versionString = `v${nextVersion}.0`;
      
      const newFile = {
        id: Date.now(),
        name: file.name,
        version: versionString,
        size: `${(file.size / (1024 * 1024)).toFixed(1)} MB`,
        uploadDate: new Date().toISOString().split('T')[0],
        description: `New version uploaded`,
        url: URL.createObjectURL(file),
        isRealPDF: true
      };
      setUploadedFiles(prev => [newFile, ...prev]);
      setSelectedFile(newFile);
    }
  };



  const handleFileSelect = (file) => {
    setSelectedFile(file);
  };

  const handleDownload = (file) => {
    // In a real app, this would trigger an actual download
    console.log(`Downloading ${file.name}`);
    // Create a temporary link element for download
    const link = document.createElement('a');
    link.href = file.url;
    link.download = file.name;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const onDocumentLoadSuccess = ({ numPages }) => {
    setNumPages(numPages);
    setPageNumber(1);
  };

  const changePage = (offset) => {
    setPageNumber(prevPageNumber => prevPageNumber + offset);
  };

  const previousPage = () => {
    changePage(-1);
  };

  const nextPage = () => {
    changePage(1);
  };

  const changeScale = (newScale) => {
    setScale(prevScale => Math.max(0.5, Math.min(2.0, prevScale + newScale)));
  };

  // Set default PDF when component mounts
  useEffect(() => {
    // Find the Mock_New_Jersey PDF and set it as default
    const mockNJPDF = uploadedFiles.find(file => 
      file.name.includes('Mock_New_Jersey') || file.name.includes('mock_new_jersey')
    );
    if (mockNJPDF) {
      setSelectedFile(mockNJPDF);
    }
  }, []); // Empty dependency array means this runs once when component mounts

  return (
    <div className="max-w-7xl mx-auto p-6">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-secondary-900 mb-2 font-serif">
          PDF Document Manager
        </h1>
        <p className="text-secondary-600">
          View, download, and manage PDF documents related to your RAG LLM queries
        </p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* File List Panel */}
        <div className="lg:col-span-1">
          <div className="bg-white rounded-lg shadow-sm border border-secondary-200 p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-semibold text-secondary-900">Previous Versions</h2>
              <label className="cursor-pointer">
                <input
                  type="file"
                  accept=".pdf"
                  onChange={handleFileUpload}
                  className="hidden"
                />
                <div className="p-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors flex items-center space-x-2">
                  <Plus className="w-4 h-4" />
                  <span className="text-sm font-medium">Update</span>
                </div>
              </label>
            </div>

            <div className="space-y-2">
              {uploadedFiles.map((file) => (
                <div
                  key={file.id}
                  className={`p-3 rounded-lg border cursor-pointer transition-all duration-200 ${
                    selectedFile?.id === file.id
                      ? "border-primary-500 bg-primary-50"
                      : "border-secondary-200 hover:border-secondary-300 hover:bg-secondary-50"
                  }`}
                  onClick={() => handleFileSelect(file)}
                >
                  <div className="flex items-center space-x-2">
                    <FileText className="w-5 h-5 text-red-500 flex-shrink-0" />
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center space-x-2 mb-1">
                        <h3 className="font-medium text-secondary-900 text-sm truncate">
                          {file.name}
                        </h3>
                      </div>
                      <p className="text-xs text-secondary-600 mb-1">
                        {file.description}
                      </p>
                      <div className="flex items-center justify-between text-xs text-secondary-500">
                        <span>{file.size}</span>
                        <span>{file.uploadDate}</span>
                      </div>
                    </div>
                    <div className="flex items-center space-x-1">
                      <span className="px-2 py-1 bg-secondary-100 text-secondary-700 text-xs font-semibold rounded">
                        {file.version}
                      </span>
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          handleDownload(file);
                        }}
                        className="px-2 py-1 bg-secondary-100 text-secondary-700 text-xs font-semibold rounded hover:bg-secondary-200 transition-colors"
                        title="Download"
                      >
                        <Download className="w-3 h-3" />
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {uploadedFiles.length === 0 && (
              <div className="text-center py-8 text-secondary-500">
                <FileText className="w-12 h-12 mx-auto mb-3 text-secondary-300" />
                <p>No documents uploaded yet</p>
                <p className="text-sm">Upload a PDF to get started</p>
              </div>
            )}
          </div>
        </div>

        {/* PDF Viewer Panel */}
        <div className="lg:col-span-2">
          <div className="bg-white rounded-lg shadow-sm border border-secondary-200 p-6">
            {selectedFile ? (
              <div>
                <div className="flex items-center justify-between mb-4">
                  <div>
                    <h2 className="text-xl font-semibold text-secondary-900">
                      {selectedFile.name}
                    </h2>
                    <p className="text-sm text-secondary-600">
                      {selectedFile.description} • {selectedFile.size} • Uploaded {selectedFile.uploadDate}
                    </p>
                  </div>
                  <div className="flex items-center space-x-2">
                    <button
                      onClick={() => handleDownload(selectedFile)}
                      className="flex items-center space-x-2 px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors"
                    >
                      <Download className="w-4 h-4" />
                      <span>Download</span>
                    </button>
                  </div>
                </div>

                {/* PDF Controls */}
                <div className="flex items-center justify-between mb-4 p-3 bg-secondary-50 rounded-lg">
                  <div className="flex items-center space-x-2">
                    <button
                      onClick={previousPage}
                      disabled={pageNumber <= 1}
                      className="px-3 py-1 bg-white border border-secondary-300 rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed hover:bg-secondary-50"
                    >
                      Previous
                    </button>
                    <span className="text-sm text-secondary-600">
                      Page {pageNumber} of {numPages || '--'}
                    </span>
                    <button
                      onClick={nextPage}
                      disabled={pageNumber >= (numPages || 1)}
                      className="px-3 py-1 bg-white border border-secondary-300 rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed hover:bg-secondary-50"
                    >
                      Next
                    </button>
                  </div>
                  <div className="flex items-center space-x-2">
                    <button
                      onClick={() => changeScale(-0.1)}
                      className="px-2 py-1 bg-white border border-secondary-300 rounded text-sm hover:bg-secondary-50"
                    >
                      -
                    </button>
                    <span className="text-sm text-secondary-600 w-12 text-center">
                      {Math.round(scale * 100)}%
                    </span>
                    <button
                      onClick={() => changeScale(0.1)}
                      className="px-2 py-1 bg-white border border-secondary-300 rounded text-sm hover:bg-secondary-50"
                    >
                      +
                    </button>
                  </div>
                </div>

                {/* Real PDF Viewer */}
                {selectedFile.isRealPDF ? (
                  <div className="border border-secondary-300 rounded-lg overflow-hidden">
                    <div className="p-4 bg-blue-50 border border-blue-200 rounded mb-4">
                      <p className="text-sm text-blue-800">
                        <strong>Debug Info:</strong> File URL: {selectedFile.url}, 
                        File Name: {selectedFile.name}
                      </p>
                    </div>
                    
                    {/* Try react-pdf first, fallback to iframe */}
                    <div className="mb-4">
                      <Document
                        file={selectedFile.url}
                        onLoadSuccess={onDocumentLoadSuccess}
                        onLoadError={(error) => {
                          console.error('PDF load error:', error);
                          console.error('File details:', selectedFile);
                        }}
                        className="flex justify-center"
                      >
                        <Page
                          pageNumber={pageNumber}
                          scale={scale}
                          className="shadow-lg"
                          onLoadError={(error) => {
                            console.error('Page load error:', error);
                          }}
                        />
                      </Document>
                    </div>
                  </div>
                ) : (
                  <div className="border-2 border-dashed border-secondary-300 rounded-lg p-8 text-center bg-secondary-50">
                    <FileText className="w-16 h-16 text-secondary-400 mx-auto mb-4" />
                    <h3 className="text-lg font-medium text-secondary-700 mb-2">
                      PDF Preview
                    </h3>
                    <p className="text-secondary-600 mb-4">
                      This is a mock display for "{selectedFile.name}"
                    </p>
                    <div className="bg-white rounded-lg p-4 max-w-md mx-auto">
                      <div className="text-left space-y-2 text-sm">
                        <p><strong>Document Type:</strong> PDF</p>
                        <p><strong>File Size:</strong> {selectedFile.size}</p>
                        <p><strong>Upload Date:</strong> {selectedFile.uploadDate}</p>
                        <p><strong>Description:</strong> {selectedFile.description}</p>
                      </div>
                    </div>
                    <p className="text-xs text-secondary-500 mt-4">
                      In a real implementation, this would show an actual PDF viewer
                    </p>
                  </div>
                )}
              </div>
            ) : (
              <div className="text-center py-16">
                <FileText className="w-16 h-16 text-secondary-300 mx-auto mb-4" />
                <h3 className="text-lg font-medium text-secondary-700 mb-2">
                  No Document Selected
                </h3>
                <p className="text-secondary-600">
                  Select a document from the list to view its contents
                </p>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default PDFViewer; 